import React from "react";
import ServicePhoto from "../../assets/triangle2.png";

const PresFiche = () => {
  return (
    <div id="presFicheID" className="presFiche">
      <img
        src={ServicePhoto}
        alt="ServicePhoto"
        aria-hidden="true"
        focusable="false"
      />
      <div className="presFichePhoto"></div>
      <div className="presFicheInfo">
        <h3>Nicolas Kwiatkowski</h3>
        <p>Développeur Web fullstack/ développeur logiciel</p>
        <p>Micro-entrepreneur sur Nîmes</p>
        <p>Formation/titre professionnel :</p>
        <ul>
          <li key="c1">
            Responsable en ingénierie des logiciels - niveau 6 (ancien niveau 2)
          </li>
          <li key="c2">Développeur web - niveau 5</li>
          <li key="c3">
            Technicien supérieur en informatique de gestion (option
            client/serveur) - niveau 5
          </li>
          <li key="c4">Maîtrise des langages : C# - niveau 6</li>
        </ul>
        <p> Langages principaux :</p>
        <ul>
          <li key="c5">C++, C#, VB.net, Java</li>
          <li key="c6">PL/SQL</li>
          <li key="c7">
            HTML5, CSS3, Javascript (sass, bootstrap, express.js, react)
          </li>
        </ul>
      </div>
    </div>
  );
};

export default PresFiche;
