import React from "react";
import ServiceIntro from "./services/ServiceIntro";
//import ServiceProjet from "./services/ServiceProjet";
//import ServicePhoto from "../assets/navbar1.webp"

const Service = () => {
  return (
    <div id="service" className="service">
      <div className="service_box box_content_center">
        <svg
          preserveAspectRatio="none"
          viewBox="0 0 1000 700"
          xmlns="http://www.w3.org/2000/svg"
          aria-hidden="true"
          focusable="false"
        >
          <polygon fill="currentColor" points="0 0 1000 0 0 700"></polygon>
        </svg>
        <div className="service_opacity">
          <div className="service_opacity_espace1"></div>
          <ServiceIntro />
          <div className="service_opacity_espace2"></div>
        </div>
      </div>
    </div>
  );
};

export default Service;
