import React from "react";

import PresDetails from "./presentation/PresDetails";
import PresFiche from "./presentation/PresFiche";

const Presentation = () => {
  const bouttonClickPres = (e) => {
    let pres_carte_element = document.getElementById("presentation_cartesId");

    if (!pres_carte_element.classList.contains("presentation_Carte_Anim")) {
      pres_carte_element.classList.toggle("presentation_Carte_Anim");
      document.getElementById("presFicheID").classList.toggle("presFicheAnim");
      document
        .getElementById("presDetailsID")
        .classList.toggle("presDetailsAnim");
    }
  };

  const supprAnim = () => {
    document
      .getElementById("presentation_cartesId")
      .classList.remove("presentation_Carte_Anim");

    if (
      document.getElementById("presFicheID").classList.contains("presFicheAnim")
    ) {
      document.getElementById("boutton_CarteID").innerHTML =
        '<i class="fas fa-angles-right" aria-hidden="true" focusable="false"></i><p>Aller vers fiche</p><i class="fas fa-angles-right" aria-hidden="true" focusable="false"></i>';
    } else {
      document.getElementById("boutton_CarteID").innerHTML =
        '<i class="fas fa-angles-left" aria-hidden="true" focusable="false"></i><p>Aller vers présentation</p><i class="fas fa-angles-left" aria-hidden="true" focusable="false"></i>';
    }
  };

  return (
    <div id="presentation" className="presentation">
      <div className="presentation_box box_content_center">
        <svg
          preserveAspectRatio="none"
          viewBox="0 0 1000 700"
          xmlns="http://www.w3.org/2000/svg"
          aria-hidden="true"
          focusable="false"
        >
          <polygon fill="currentColor" points="0 0 1000 0 1000 700"></polygon>
        </svg>
        <div className="presentation_opacity">
          <div className="presentation_espace1"></div>

          <h2>Qui suis-je?</h2>

          <div
            id="presentation_cartesId"
            className="presentation_Carte"
            onAnimationEnd={supprAnim}
          >
            <PresFiche />
            <PresDetails />
            <button
              id="boutton_CarteID"
              className="boutton_Carte "
              onClick={bouttonClickPres}
            >
              <i
                className="fas fa-angles-left ihaut"
                aria-hidden="true"
                focusable="false"
              ></i>
              <p>Aller vers présentation</p>
              <i
                className="fas fa-angles-left ibas"
                aria-hidden="true"
                focusable="false"
              ></i>
            </button>
          </div>
          <div className="presentation_espace2"></div>
        </div>
      </div>
    </div>
  );
};

export default Presentation;
