import React from "react";
import Introtriangle1 from "../assets/triangle1.png";
import Introtriangle2 from "../assets/triangle2.png";

const Intro = () => {
  let ticking = false;

  function isWholeVisible(element) {
    let cibleElement = document
      .getElementsByClassName(element)[0]
      .getBoundingClientRect();

    if (cibleElement) {
      return (
        cibleElement.top + 20 - window.innerHeight < 1 &&
        cibleElement.bottom > 1
      );
    }
  }

  const DeclacheAnimeTriangle = () => {
    let triAnim;

    for (let i = 1; i < 3; i++) {
      if (isWholeVisible(`intro_Block${i}`)) {
        triAnim = document.querySelectorAll(`.triangle${i}, .intro_p${i}`);
        if (triAnim) {
          console.log(triAnim);
          triAnim.forEach((item) => {
            item.classList.remove(`triangle${i}_anim`, `intro_p${i}Anim`);
          });
        }

        /* triAnim = null;

        triAnim = document.querySelectorAll(`.intro_p${i}Anim`);
        if (triAnim) {
          triAnim.forEach((item) => {
            if (item.classList.contains(`intro_p${i}Anim`)) {
              item.classList.remove(`intro_p${i}Anim`);
            } else {
              return;
            }
          });
        }*/
      }
    }
  };

  window.addEventListener("scroll", function (e) {
    if (!ticking) {
      window.requestAnimationFrame(function () {
        DeclacheAnimeTriangle();
        ticking = false;
      });

      ticking = true;
    }
  });

  window.addEventListener("load", function (e) {
    DeclacheAnimeTriangle();
  });

  return (
    <div className="intro">
      <div className="intro_box box_content_center">
        <div className="intro_opacity">
          <div className="intro_opacity_espace1"></div>
          <div className="intro_Block1">
            <img
              className="triangle1 triangle1_anim"
              src={Introtriangle1}
              alt="triangle droite"
              aria-hidden="true"
              focusable="false"
            ></img>
            <p className="intro_p1 intro_p1Anim">
              Vous avez un <strong>projet à réaliser</strong>? Vous voulez
              partir à la conquête du web ou tout simplement
              <strong>
                {" "}
                ajouter des fonctionnalités, assurer la maintenance
              </strong>{" "}
              de vos projets existants? Vous souhaitez
              <strong> automatiser et fluidifier</strong> le fonctionnement de
              votre
              <strong> entreprise</strong> grâce au <strong>numérique</strong>?
            </p>
          </div>
          <div className="intro_opacity_espace2"></div>
          <div className="intro_Block2">
            <img
              className="triangle2 triangle2_anim"
              src={Introtriangle2}
              alt="triangle gauche"
              aria-hidden="true"
              focusable="false"
            ></img>
            <p className="intro_p2 intro_p2Anim">
              Que vous soyez
              <strong> un particulier, une association, une TPE/PME</strong>,
              mon savoir-faire et mes compétences vous accompagneront sur un
              large panel de projets qui va de la{" "}
              <strong>création de logiciels</strong> PC sur-mesure (progiciel,
              gestion, automatisation, etc...) à la
              <strong> création de site web</strong> (vitrine, e-commerce, blog,
              API, etc...) en passant par les <strong>applications web</strong>.
            </p>
          </div>
          <div className="intro_opacity_espace1"></div>
        </div>
      </div>
    </div>
  );
};

export default Intro;
