import React from "react";

const LoadSpinner = () => {
  window.addEventListener("load", function (event) {
    document.getElementById("loadSpinnerID").classList.add("hide");
  });

  return (
    <div id="loadSpinnerID" className="loadSpinner">
      <i className="fas fa-spinner fa-pulse" aria-hidden="true"
        focusable="false"></i>
      <p>Chargement</p>
    </div>
  );
};

export default LoadSpinner;
