import React from "react";

const BandeauPlatforme = () => {
  return (
    <div className="bandeauPlatforme">
      <div className="bandeauPlatforme_box box_content_center">
        <div className="bandeauPlatforme_opacity">
          <div className="bandeauPlatforme_contenu">
            <div className="bandeauPlatforme_cercle">
              <i
                className="fas fa-display"
                aria-hidden="true"
                focusable="false"
              ></i>
            </div>
            <div className="bandeauPlatforme_cercle">
              <i
                className="fas fa-laptop"
                aria-hidden="true"
                focusable="false"
              ></i>
            </div>
          </div>
          <div className="bandeauPlatforme_contenu">
            <div className="bandeauPlatforme_cercle">
              <i
                className="fas fa-tablet-screen-button"
                aria-hidden="true"
                focusable="false"
              ></i>
            </div>
            <div className="bandeauPlatforme_cercle">
              <i
                className="fas fa-mobile-screen"
                aria-hidden="true"
                focusable="false"
              ></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BandeauPlatforme;
