import React from "react";

const ServiceIntro = () => {
  let ticking = false;

  function isWholeVisible(element) {
    let cibleElement = document
      .getElementsByClassName(element)[0]
      .getBoundingClientRect();

    if (cibleElement) {
      return (
        cibleElement.top + 70 - window.innerHeight < 1 &&
        cibleElement.bottom > 1
      );
    }
  }

  const DeclacheAnimeUL = () => {
    let ulAnim;
    let i = 1;

    if (isWholeVisible("servicesConteneur")) {
      ulAnim = document.querySelectorAll(".serviceUl");

      if (ulAnim) {
        ulAnim.forEach((item) => {
          if (item.classList.contains("ulAnimation")) {
            return;
          } else {
            item.classList.add("ulAnimation" + i);
            i++;
          }
        });
      }
    }
  };

  window.addEventListener("scroll", function (e) {
    if (!ticking) {
      window.requestAnimationFrame(function () {
        DeclacheAnimeUL();
        ticking = false;
      });

      ticking = true;
    }
  });
  
  window.addEventListener("load", function (e) {
    DeclacheAnimeUL();
  });

  return (
    <div className="serviceIntro">
      <h2>Services web et logiciel</h2>
      <div>
        <p>
          Les divers services proposés ci-dessous sont à titre indicatif. Bien
          entendu, ils s'<strong>adaptent à vos perspectives et besoins</strong>
          .
        </p>
        <div className="servicesConteneur">
          <ul className="serviceUl">
            <li key="b1">Logiciel PC sur-mesure</li>
            <li key="b2">Application web / web progressive</li>
          </ul>
          <ul className="serviceUl">
            <li key="b3">Site vitrine</li>
            <li key="b4">E-commerce (sur-mesure ou CMS)</li>
            <li key="b5">Blog</li>
            <li key="b6">Forum</li>
            <li key="b7">Site intranet</li>
            <li key="b8">Serveur d'API / web service</li>
          </ul>
          <ul className="serviceUl">
            <li key="b9">Refonte / maintenance de site</li>
            <li key="b10">
              Amélioration de la visibilité dans les moteurs de recherche
            </li>
            <li key="b11">Amélioration de l'accessiblité de site web</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default ServiceIntro;
