import React from "react";
import { NavLink } from "react-router-dom";

const Sousmenu = (props) => {
  return (
    <div
      className={
        "sousmenu" +
        (props.etat_fixe ? " sousmenu_fixed" : "") +
        (props.cacher_nonFixe && !props.etat_fixe ? " cacher_nonFixe" : "")
      }
    >
      {props.ancre ? (
        <a className="nav-link" href={props.cible}>
          <i
            className={
              "fas fa-caret-right" + (props.etat_fixe ? "" : " rotation90deg")
            }
          ></i>
          {props.texte}
        </a>
      ) : (
        <NavLink
          className={({ isActive }) =>
            "nav-link" + (isActive ? " nav-active" : "")
          }
          to={props.cible}
          end="true"
        >
          {props.etat_fixe && <i className="fas fa-at"></i>}
          {props.texte}
        </NavLink>
      )}
    </div>
  );
};

export default Sousmenu;
