import React from "react";
import ServicePhoto2 from "../../assets/triangle1.png";
// smiley qui rie en transpirant  &#x1F605;

const PresDetails = () => {
  return (
    <div id="presDetailsID" className="presDetails">
      <img
        src={ServicePhoto2}
        alt="ServicePhoto"
        aria-hidden="true"
        focusable="false"
      />
      <p className="presDetails_Intro">
        Pour me présenter, et de façon personnelle, je commencerais par ce qui
        m'a plu dès le début dans le développement, qui est d'utiliser des
        concepts logiques pour arriver à une finalité créative. Pour être plus
        explicite, je me considère être un artisan, à l'image d'un ébéniste qui
        élabore un meuble en assemblant toutes les parties qu'il a créé et/ou
        récupéré.
      </p>
      <p>
        Etant autodidacte, j'ai commencé à apprendre des langages de
        programmation pour mon plaisir personnel bien avant de vouloir en faire
        mon métier dans les années 2000.Cette passion ne m'a dès lors jamais
        quitté, ni même, ma soif de connaissances.
      </p>
      <p>
        Après diverses formations et expériences, mon envie d'autonomie m'a
        poussé vers l'auto-entreprenariat.
      </p>
      <p>
        J'ai donc décidé de récolter le fruit de mes compétences pour moi et de
        me servir de ces mêmes compétences, pour apporter mon aide et mon
        expérience à ceux qui en ont besoin.
      </p>
    </div>
  );
};

export default PresDetails;
